import {
  encodeAddress,
  decodeAddress
} from "@polkadot/util-crypto"
import store from "../store"

import {
  $t
} from '../i18n'

import {
  getApi,
  token2Uni,
  stanfiAddress,
  getTxPaymentInfo
} from './polkadot'
import {
  injectAccount
} from './account'
/**
 * 
 */
export const subBonded = async () => {
  let subBonded = store.state.subBonded
  try {
    subBonded()
  } catch (e) {}
  const api = await getApi()
  subBonded = await api.query.staking.bonded(store.state.account.address, (bonded) => {
    if (!bonded.toJSON()) {
      store.commit('saveBonded', null)
      return;
    }
    store.commit('saveBonded', bonded.toJSON())
  })
  store.commit('saveSubBonded', subBonded)
}

/**
 * 
 */
export const subNominators = async () => {
  let subNominators = store.state.subNominators
  try {
    subNominators()
  } catch (e) {}
  const api = await getApi()
  const {validators} = await api.derive.staking.overview()

  store.commit('saveLoadingStaking', true)
// 
  const nominators = await api.query.staking.nominators(store.state.account.address, async (nominators) => {
    if (!nominators.toJSON()) {
      store.commit('saveNominators', [])
      store.commit('saveLoadingStaking', false)
      return;
    }
    // 
    let infos = await Promise.all(nominators.toJSON().targets.map(v => api.derive.accounts.info(v)))
    infos = infos.map(acc => {
      let nick = ''
      let address = stanfiAddress(acc.accountId)
      if (acc.identity?.displayParent){
        if (acc.identity?.display){
          nick = acc.identity.displayParent + ' (' + acc.identity.display + ')'
        }else{
          nick = acc.identity.displayParent
        }
      }else{
        nick = `${address.slice(0,16)}...${address.slice(-5)}`
      }
      return {
        address: stanfiAddress(acc.accountId),
        nick
      }
    })
    // 
    const currentEra = await api.query.staking.currentEra()
    for (let i = 0; i<infos.length; i++){
      const addr = infos[i].address
      const validatorStake = await api.query.staking.erasStakers(currentEra.toString(), addr)
      const validatorComissionRate = await api.query.staking.erasValidatorPrefs(currentEra.toString(), addr)
      const validatorTotalStake = parseInt(validatorStake['total'].toString() / 1e10)
      const validatorOwnStake = parseInt(validatorStake['own'].toString() / 1e10)
      const validatorNominators = validatorStake['others'].toJSON()
      infos[i]['otherStake'] = (validatorTotalStake - validatorOwnStake) + '(' + validatorNominators.length + ')'
      infos[i]['ownStake'] = validatorOwnStake
      infos[i]['commission'] = (validatorComissionRate['commission'].toString() / 10000000).toFixed(1) + '%'
    }
    store.commit('saveLoadingStaking', false)
    store.commit('saveNominators', infos)
  })
  store.commit('saveSubNominators', subNominators)
}
/**
 * , 
 * @param {Array} validators 
 * @param {string} communityId id
 * @param {string} projectId id
 * @param {function} toast toast
 * @param {function} callback callback
 */
export const nominate = async (validators, communityId, projectId, toast, callback) => {
    const from = store.state.account && store.state.account.address
    communityId = stanfiAddress(communityId)
    projectId = stanfiAddress(projectId)
    if (!from) {
      reject('no account')
    }
    const api = await injectAccount(store.state.account)
    const nominatorTx = api.tx.staking.nominate(validators)
    const remark = encodeRemark(communityId, projectId)
    const remarkTx = api.tx.system.remarkWithEvent(remark)
    const nonce = (await api.query.system.account(from)).nonce.toNumber()
  
    const unsub = await api.tx.utility
      .batch([nominatorTx, remarkTx]).signAndSend(from, {
        nonce
      }, ({
        status,
        dispatchError
      }) => {
        try {
          handelBlockState(status, dispatchError, toast, callback, unsub)
        } catch (e) {
          toast(e.message, {
            title: $t('tip.error'),
            variant: 'danger'
          })
        }
      })
}

/**
 *  DOT
 * @param {number} amount DOT DOT
 * @param {Array} validators 
 * @param {string} communityId id
 * @param {string} projectId id
 * @param {function} toast toast
 * @param {function} callback callback
 */
export const bondAndNominate = async (amount, validators, communityId, projectId, toast, callback) => {
  const from = store.state.account && store.state.account.address
  communityId = stanfiAddress(communityId)
  projectId = stanfiAddress(projectId)
  if (!from) {
    reject('no account')
  }
  const api = await injectAccount(store.state.account)
  const uni = api.createType('Compact<BalanceOf>', token2Uni(amount))
  const bondTx = api.tx.staking.bond(store.state.account.address, uni, {
    Staked: null
  })

  const nominatorTx = api.tx.staking.nominate(validators)
  const remark = encodeRemark(communityId, projectId)
  const remarkTx = api.tx.system.remarkWithEvent(remark)
  const nonce = (await api.query.system.account(from)).nonce.toNumber()


  //   const bondFee = await getTxPaymentInfo(api.tx.utility.batch([bondTx, nominatorTx, remarkTx]))
  //   console.log('bondfee', bondFee);

  const unsub = await api.tx.utility
    .batch([bondTx, nominatorTx, remarkTx]).signAndSend(from, {
      nonce
    }, ({
      status,
      dispatchError
    }) => {
      try {
        handelBlockState(status, dispatchError, toast, callback, unsub)
      } catch (e) {
        toast(e.message, {
          title: $t('tip.error'),
          variant: 'danger'
        })
      }
    })
}

/**
 *  block
 * @param {*} status 
 * @param {*} dispatchError 
 * @param {*} toast toast
 * @param {*} callback callback
 * @param {*} unsub unsub
 * @returns 
 */
function handelBlockState(status, dispatchError, toast, callback, unsub) {
  if (status.isInBlock || status.isFinalized) {
    if (dispatchError) {
      let errMsg = ''
      if (dispatchError.isModule) {
        // for module errors, we have the section indexed, lookup
        const decoded = api.registry.findMetaError(dispatchError.asModule);
        const {
          documentation,
          name,
          section
        } = decoded;
        errMsg = `${section}.${name}: ${documentation.join(' ')}`
        console.log(`${section}.${name}: ${documentation.join(' ')}`);
      } else {
        // Other, CannotLookup, BadOrigin, no extra info
        console.log(dispatchError.toString());
        errMsg = dispatchError.toString()
      }
      toast(errMsg, {
        title: $t('tip.error'),
        variant: 'danger'
      })
      unsub()
      return false
    }
  }
  if (status.isBroadcast) {
    if (callback) callback()
    setTimeout(() => {
      toast($t('transaction.broadcasting'), {
        title: $t('tip.tips'),
        autoHideDelay: 5000,
        variant: 'warning'
      })
    }, 700);
  } else if (status.isInBlock) {
    console.log("Transaction included at blockHash.", status.asInBlock.toJSON());
    toast($t('transaction.inBlock'), {
      title: $t('tip.tips'),
      autoHideDelay: 6000,
      variant: 'warning'
    })
  } else if (status.isFinalized) {
    unsub()
    toast($t('transaction.nominateOk'), {
      title: $t('tip.success'),
      autoHideDelay: 5000,
      variant: "success",
    });
    // daemon
    return true
  }
}

export function encodeRemark(communityId, projectId) {
  // 01 dot
  let buf = new Uint8Array(65)
  buf[0] = 1;
  buf.set(decodeAddress(communityId), 1);
  buf.set(decodeAddress(projectId), 33)
  return '0x' + Buffer.from(buf).toString('hex')
}
